/* Header.css */
.header {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 150px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "header-title"
        "header-description";
    grid-area: header;
}

.header-title {
    grid-area: header-title;
    align-self: end;
    height: 60px;
    margin: 10px;
}

.header-description {
    grid-area: header-description;
    margin: 10px;
}