/* Search.css */
.search {
    grid-area: search;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "search-input"
        "search-suggestions";

    position: relative;
}

.search-input {
    grid-area: search-input;
    justify-self: center;
    align-self: center;
    height: 50px;

    font-size: 18px;
    border-radius: 50px;
    outline: none;
    width: 80%;
    background-color: #000505;
    color: rgb(204, 204, 204);
    border: 1px solid #333333;
    padding-left: 60px;
}

.search-suggestions {
    grid-area: search-suggestions;
    margin: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10px;
    height: 100%;
}

.suggestion-item {
    color: rgb(155, 155, 155);
    background-color: transparent;
    border: 1px rgb(155, 155, 155) solid;
    cursor: pointer;
    text-align: left;
    padding: 5px 10px;
    white-space: nowrap;
    transition: color 0.3s ease, border-color 0.3s ease;
}

.suggestion-item:hover,
.suggestion-item.highlighted {
    color: white;
    border-color: white;
    background-color: rgba(0, 0, 0, 0.1);
}

.clear-text {
    position: absolute;
    top: 48px;
    right: 10%;
    transform: translateY(-50%);
    cursor: pointer;
    color: rgb(204, 204, 204);
    background-color: transparent;
    border: none;
    font-size: 0.8em;
    z-index: 1;
}

.search-icon {
    position: absolute;
    left: 9%;
    top: 50px;
    transform: translateY(-50%);
    height: 22px;
    width: 22px;
    z-index: 1;
}
