/* StickyHeader.css */
.sticky-header {
    border-bottom: 1px var(--font-color) solid;
    display: grid;
    grid-template-columns: 15% 70% 15%;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 1000;
    align-items: center;
    height: 70px;
    background-color: var(--background-color);
    color: rgb(153, 153, 153);
}

.fixed-chart-votes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0 10px;
    grid-template-areas:
        "chart-votes-abstained chart-votes-against chart-votes-for";
    align-items: center;

    margin-left: 2.5%;
}
.right-header-item {
    padding-left: 5px;
    align-items: center;
    color: var(--font-color);
}

.left-header-item {
    text-align: right;
    padding-right: 10px;
    align-items: center;
    color: var(--font-color);
}

.highlight {
    color: var(--font-color);
    font-weight: bold;
}

.mp-highlight {
    text-decoration: underline var(--font-color);
}
