/* Chart.css */
.chart {
    grid-area: chart;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 70px 5000px 600px;
    gap: 0px 0px;
    grid-template-areas:
        "chart-votes"
        "chart-main"
        "chart-end";

    width: 100%;
}

.chart-votes, .chart-votes.fixed {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0 10px;
    grid-template-areas:
        "chart-votes-abstained chart-votes-against chart-votes-for";
    align-items: center;

    margin-left: 2.5%;
}

.chart-votes {
    position: relative;
}

.chart-votes.fixed {
    position: fixed;
    width: 80%;
    height: 70px;
    top: 0;
    max-width: 1500px;
}

.chart-main {
    grid-area: chart-main;
}

.chart-end {
    grid-area: chart-end;}
