/* App.css */
@font-face {
    font-family: 'font1';
    src: local('font1'), url('./assets/fonts/font1.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'font2';
    src: local('font2'), url('./assets/fonts/font2.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

/* Variables */
:root {
    --background-color: #000505;
    --font-color: rgb(225, 225, 225);
}

/* Global Styles */
body {
    margin: 0;
    overflow-x: hidden;
    font-family: 'font1', sans-serif;
    background-color: var(--background-color);
    color: var(--font-color);
}

/* Container Styles */
.container {
    display: grid;
    grid-template-columns: 10% 80% 10%;
    grid-template-rows: 500px 200px 80px 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    justify-content: center;
    grid-template-areas:
        ". header ."
        ". search ."
        ". legend ."
        "dates chart motions";
    width: 100vw;
    max-width: 1500px;
    margin: 0 auto;
}

/* Media Queries */
@media (max-width: 1000px) {
    .container {
        grid-template-areas:
            "header header header"
            "search search search"
            "legend legend legend"
            "dates chart motions";
    }

    .search-input {
        width: 80%;
    }

    .header {
        margin: 0 20px 0 20px;
    }

    .legend {
        margin: 0 20px 0 20px;
    }
}
