/* Legend.css */
.legend {
    grid-area: legend;
}

.legend-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.legend-item {
    display: flex;
    align-items: center;
    margin-right: 10px;
}

.color-box {
    width: 10px;
    height: 10px;
    margin-right: 5px;
}
